export default function (Component) {
Component.options.__tip = "<p>There are multiple ways to show a modal.</p>\n<p><strong>Using the <code>v-fd-open-modal</code>-directive</strong></p>\n<p>You can use the <code>v-fd-open-modal</code>-directive on anything that emits <code>click</code>-events. The argument of the <code>v-fd-open-modal</code>-directive is the name of the modal you want to show. The name if a modal is specified by setting the <code>ref</code>-attribute. The example below shows the party-modal using this technique.</p>\n<p><strong>Manually showing the modal</strong></p>\n<p>Of course you can also manually show or close the modal by simply calling the corresponding methods in it:</p>\n<ul>\n<li><code>show(event?)</code>: shows the modal.</li>\n<li><code>close(event?)</code>: closes the modal.</li>\n</ul>\n"
}